import loadable from "@loadable/component";
import ParticleSystem from "./particleSystem";
import { useParticles } from "../../UI/hooks/useParticles";
import { useEffect } from "react";

type AnimatedBackgroundProps = {};

const AnimatedBackground: React.FC<AnimatedBackgroundProps> = ({}) => {
  let particleSystem: ParticleSystem | undefined;
  const { particlesAmount } = useParticles();
  console.log("THIE AMOUNT", particlesAmount);
  useEffect(() => {
    console.log("THE JANDER KNA", particleSystem, particlesAmount);
    if (particleSystem) {
      console.log("JANDEr");
      particleSystem.setParticles(particlesAmount);
    }
  }, [particlesAmount]);

  const setup = (p5, canvasParentRef) => {
    p5.createCanvas(window.innerWidth, window.innerHeight).parent(
      canvasParentRef
    );
    p5.strokeWeight(0.5);
    particleSystem = new ParticleSystem(p5);
    particleSystem.setParticles(particlesAmount);
    console.log("CALLING SETUP");
  };

  const draw = (p5) => {
    p5.background(255, 100, 255, 0);
    p5.clear();
    p5.noStroke();
    particleSystem.update();
  };

  if (typeof window == "undefined") return null;
  const Sketch = loadable(() => import("react-p5"));

  return (
    <div className="sketchContainer">
      <Sketch setup={setup} draw={draw} />;
    </div>
  );
};
export default AnimatedBackground;
